<template>
  <div id="app">
    <!-- <img src="./assets/logo.png"> -->
    <!-- <router-view/> -->
    <p>{{toc}}</p>
    <button disable @click="getSearch()">显示常用列表</button>
    <div v-for="(item,index) in commonArr" :key="index">
      <a href="#">{{ item.title }}</a>
    </div>
  </div>
</template>

<script>
// import fly from '@/utils/fly'
import axios from 'axios'

export default {
  data() {
    return {
      toc: '',
      commonArr: [],
    }
  },
  methods: {
    getlist() {
      // /repos/pdk/sxf/docs/wflp2g
      // /repos/76893/docs
      // /repos/pdk/sxf/docs
      // /users/hangzhenhui/repos
      axios.get('/api/v2/repos/76893/toc').then((res) => {
        // eslint-disable-next-line no-console
        // console.log('启动循环')
        // 重新组装目录
        const dataArr = res.data.data
        // const commonArr = []
        for (let i = 0; i < dataArr.length; i += 1) {
          // eslint-disable-next-line no-console
          if (dataArr[i].title === '规则') {
            break
          }
          // eslint-disable-next-line no-console
          // console.log(dataArr[i])
          this.commonArr.push(dataArr[i])
          // eslint-disable-next-line no-console
          // console.log(commonArr)
        }
        // eslint-disable-next-line no-console
        // console.log(`常用内容为${common}`)

        // 之前的尝试↓
        // eslint-disable-next-line
        // console.log(res.data+'+'+this.$url)
        // console.log(res.data.data.toc)
        // const _data = res.data.data.toc
        // this.toc = res.data.data.toc.split('\n')
        // this.toc = res.data.data.toc
        // console.log(typeof res.data.data.toc)
        // const regRN = /\r\n/g
        // const arr = []
        // const aa = res.data.data.toc
        // const arr = aa.split('\n')
        // eslint-disable-next-line no-console
        // console.log(arr)
        // const str = 'How are you doing today?'
        // console.log(str.split(' '))
      })
    },
    getSearch() {
      // axios.get('/api/v2/search/repos?q=aa&type=Book').then((searchres) => {
      //   // eslint-disable-next-line no-console
      //   console.log(searchres)
      // })
      // eslint-disable-next-line no-console
      console.log(this.commonArr)
    },
  },
  /* eslint spaced-comment: ["error", "always"] */
  mounted() {
    this.getlist()
  },
}
</script>

<style>
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
  margin-top: 60px;
}
</style>



